import React, { Component } from "react";
import { Button, Progress } from "semantic-ui-react";

export default class ProjectProgress extends Component {
  state = { percent: 33 };

  render() {
    let progress;
    if (this.props.selectedProject.progress === null) {
      progress = 0;
    } else {
      progress =
        (this.props.selectedProject.progress / this.props.selectedProgress) *
        100;
    }

    return (
      <div>
        <Progress percent={progress} indicating />
      </div>
    );
  }
}
